@import url(https://fonts.googleapis.com/css?family=Roboto:300);
body {
  background: #141414;
  color: #ffffff;
  padding-left: 10px;
}

.apes {
  float: right;
}

.movie-app > .row {
  overflow-x: auto;
  flex-wrap: nowrap;
  display: flex;
  overflow-y: hidden;
  margin-bottom: 15px;
}

.image-container {
  position: relative;
  transition: transform 0.2s;
  width: 310px;
  padding: 20px;
}

.image-container:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.image-container:hover .overlay {
  opacity: 1;
}

.overlay {
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  width: 310px;
  transition: 0.5s ease;
  opacity: 0;
  top: 0;
  font-size: 20px;
  padding: 69px;
  text-align: center;
}

.remove {
  padding-left: 5px;
}

.wsb {
  width: 50px;
}
@media only screen and (max-width: 975px) {
    .wsb {
        display: none;
    }

    .movieHeading {
        display: unset!important;
    }

    .searchBox {
        max-width: 90%;
    }
}
